export default angular.module('deskworks.DoorflowProduct', [
    'ngResource',
    'deskworks.config',
])

    .factory('DoorflowProduct', function($resource, deskworksConfig) {
        const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/centers/:centerId/products/:productId'), {}, {
            get: { method: 'GET' },
            update: { method: 'PUT' },
        });

        return {
            get: function(centerId, productId) {
                return res.get({ centerId, productId }).$promise;
            },

            save: function(centerId, productId, data) {
                return res.update({ centerId, productId }, data).$promise;
            },
        };
    });
