export default angular.module('deskworks.DoorflowCenter', [
  'ngResource',
  'deskworks.config',
])

  .factory('DoorflowCenter', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/centers/:centerId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query().$promise,

      save: data => res.update({ centerId: data.id }, data).$promise,
    };
  });
