/**
 * Integrations Setup -> DoorFlow Settings directive
 */
export default angular.module('deskworks.doorflowSettingsDirective', [
  'debounce',
  'deskworks.DoorflowSettings',
  'deskworks.DoorflowCenter',
  'deskworks.DoorflowSite',
  'deskworks.DoorflowAccessGroup',
  'deskworks.center.service',
])

  .directive('doorflowSettings', function() {
    const templateUrl = 'settings/doorflow-settings.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'DoorflowSettingsCtrl as $ctrl',
    };
  })

  .controller('DoorflowSettingsCtrl', function(dwAlerts, $q, debounce, helper, centerService,
              DoorflowSettings, DoorflowCenter, DoorflowAccessGroup, DoorflowSite) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load settings
     */
    $ctrl.load = () => {
      const promises = {
        doorflowSettings: DoorflowSettings.get()
          .catch(err => dwAlerts.error(err, 'Failed to load DoorFlow settings.')),
        centers: centerService.getCenters()
          .catch(err => dwAlerts.error(err, 'Failed to query centers.')),
        doorflowCenters: DoorflowCenter.query()
          .catch(err => dwAlerts.error(err, 'Failed to query DoorFlow centers.')),
        doorflowSites: DoorflowSite.query()
          .catch(err => dwAlerts.error(err, 'Failed to query DoorFlow sites.')),
      };

      $ctrl.spin = true;

      return $q.all(promises)
        .then(result => {
          $ctrl.doorflowSettings = result.doorflowSettings;
          $ctrl.doorflowCenters = result.doorflowCenters;
          $ctrl.doorflowSites = result.doorflowSites;
          return result;
        })
        .finally(() => { $ctrl.spin = false; });
    };

    /**
     * Save DoorFlow settings
     */
    $ctrl.saveSettings = debounce(saveSettings, 1000);
    function saveSettings(form) {
      if (form && form.$invalid) return;
      $ctrl.spin = true;
      return DoorflowSettings.save($ctrl.doorflowSettings)
        .catch(err => dwAlerts.error(err, 'Failed to save DoorFlow settings.'))
        .finally(() => { $ctrl.spin = false; })
        .then(() => {
          delete $ctrl.doorflowCenters;
          return $ctrl.load();
        });
    }

    /**
     * Save DoorFlow center
     */
    $ctrl.saveCenter = debounce(saveCenter, 500);
    function saveCenter(center, form) {
      if (form && form.$invalid) return;
      center.$$spin = true;
      return DoorflowCenter.save(center)
        .finally(() => { center.$$spin = false; })
        .catch(err => dwAlerts.error(err, 'Failed to save DoorFlow center.'));
    }

    /**
     * Change DoorFlow Site handler
     */
    $ctrl.changeSite = (center, form) => {
      return $ctrl.saveCenter(center, form);
    };
  });
