export default angular.module('deskworks.DoorflowUser', [
  'ngResource',
  'deskworks.config',
])

  .factory('DoorflowUser', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/centers/:centerId/users/:userId'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: (centerId, userId) => res.get({ centerId, userId }).$promise,

      save: (centerId, data) => res.update({ centerId, userId: data.id }, data).$promise,
    };
  });
