export default angular.module('deskworks.DoorflowSettings', [
  'ngResource',
  'deskworks.config',
])

  .factory('DoorflowSettings', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/settings'), {}, {
      update: { method: 'PUT' },
    });

    return {
      get: () => res.get().$promise,

      save: data => res.update({}, data).$promise,
    };
  });
