/**
 * Member -> DoorFlow User Access directive
 */
export default angular.module('deskworks.doorflowUserAccessDirective', [
  'deskworks.DoorflowCenter',
  'deskworks.DoorflowUser',
])

  .directive('doorflowUserAccess', function() {
    const templateUrl = 'user-access/doorflow-user-access.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'DoorflowUserAccessCtrl as $ctrl',
      bindToController: {
        userId: '<',
        save: '=',
      },
    };
  })

  .controller('DoorflowUserAccessCtrl', function($q, dwAlerts, helper, state, DoorflowCenter, DoorflowUser) {
    const $ctrl = this;

    $ctrl.$onInit = () => {
      $ctrl.load();
    };

    /**
     * Load data
     */
    $ctrl.load = () => {
      return DoorflowCenter.query()
        .catch(err => dwAlerts.error(err, 'Failed to query DoorFlow centers.'))
        .then(doorflowCenters => {
          $ctrl.doorflowCenter = helper.find(doorflowCenters, state.getCurrentCenterId());

          if (!$ctrl.doorflowCenter || !$ctrl.doorflowCenter.enabled) return;

          const promises = {};

          if ($ctrl.userId && $ctrl.userId !== 'new' && !helper.isUid($ctrl.userId)) {
            promises.doorflowUser = DoorflowUser.get(state.getCurrentCenterId(), $ctrl.userId)
              .catch(err => dwAlerts.error(err, 'Failed to get DoorFlow user.'));
          }

          return $q.all(promises)
            .then(result => {
              if (result.doorflowUser) {
                $ctrl.doorflowUser = result.doorflowUser;
              } else {
                $ctrl.doorflowUser = { doorflowEnabled: false };
              }

              $ctrl.save = save;
            });
        });
    };

    /**
     * Save DoorFlow user
     */
    function save() {
      if (!$ctrl.doorflowCenter.enabled) return;

      $ctrl.doorflowUser.id = $ctrl.userId;
      return DoorflowUser.save(state.getCurrentCenterId(), $ctrl.doorflowUser)
        .catch(err => dwAlerts.error(err, 'Failed to save DoorFlow user.'));
    }
  });
