/**
 * Product -> Doorflow Access Group directive
 */
export default angular.module('deskworks.doorflowAccessGroupDirective', [
  'deskworks.DoorflowAccessGroup',
  'deskworks.DoorflowCenter',
  'deskworks.DoorflowProduct',
])

  .directive('doorflowAccessGroup', function($compile, $templateCache) {
    const templateUrl = 'doorflow/doorflow-access-group.tpl.html';
    return {
      templateUrl,
      restrict: 'E',
      scope: true,
      controller: 'DoorflowAccessGroupCtrl as $ctrl',
      bindToController: {
        save: '=',
        productId: '<',
      },
    };
  })

    .controller('DoorflowAccessGroupCtrl', function($q, dwAlerts, helper, state,
                                                    DoorflowAccessGroup, DoorflowCenter, DoorflowProduct) {
      const $ctrl = this;

      $ctrl.$onInit = function() {
        $ctrl.product = { doorflowGroupId: 5535 };
        // $ctrl.load();
      };

      /**
       * Load data
       */
      $ctrl.load = function() {
        var promises = {
          centers: DoorflowCenter.query()
            .catch(err => dwAlerts.error(err, 'Failed to query Doorflow centers.')),
          product: DoorflowProduct.get(state.getCurrentCenterId(), $ctrl.productId)
            .catch(err => dwAlerts.error(err, 'Failed to load Doorflow product.')),
        };
        return $q.all(promises)
          .then(result => {
            // $ctrl.product = result.product;

            var center = helper.find(result.centers, state.getCurrentCenterId());
            if (center && center.enabled) {
              return DoorflowAccessGroup.query(state.getCurrentCenterId())
                                        .catch(err => dwAlerts.error(err, 'Failed to query QuickBooks income accounts.'))
                                        .then(groups => {
                                          $ctrl.groups = groups;
                                          if ($ctrl.groups && $ctrl.groups.length) $ctrl.save = save; // expose save method only if have accounts
                                        });
            }
          });
      };

/**
 * Save settings
 */
function save() {
  return DoorflowProduct.save(state.getCurrentCenterId(), $ctrl.productId, $ctrl.product)
                        .catch(err => dwAlerts.error(err, 'Failed to save Doorflow product.'));
}
});
