export default angular.module('deskworks.DoorflowAccessGroup', [
  'ngResource',
  'deskworks.config',
])

  .factory('DoorflowAccessGroup', function($resource, deskworksConfig) {
    const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/centers/:centerId/doorflow-groups'));

    return {
      query: centerId => {
        return res.query({ centerId }).$promise;
      },
    };
  });
