export default angular.module('deskworks.DoorflowSite', [
  'ngResource',
  'deskworks.config',
])

  .factory('DoorflowSite', function($resource, deskworksConfig) {

    const res = $resource(deskworksConfig.getApiAbsUrl('/doorflow/doorflow-sites'), {}, {
      update: { method: 'PUT' },
    });

    return {
      query: () => res.query().$promise,
    };
  });
